<template >
  <div v-if="bookDetailData !== null">
    <div
      class="read"
      :style="{
        background: bgc.bg,
        color: bgc.color,
        height: isHeight ? '100vh' : '100%',
      }"
      v-if="bookDetailData.contentType == 'txt'"
      @click="CtrlHandler"
    >
      <div v-if="content && !content.isVip">
        <div v-for="(text, i) in textArr" :key="i + 'a'" class="text-list">
          <h2 :style="{ color: bgc.color }">{{ textArr[i].title }}</h2>
          <p
            v-for="(item, index) in textArr[i].arr"
            :key="index"
            :style="{
              fontSize: fontSize + 'px',
              color: bgc.color,
              textIndent: fontSize * 2 + 12 + 'px',
              'letter-spacing': '5px',
            }"
          >
            <!-- margin: 5 + 'px' + 0, -->
            {{ item.trim() }}
          </p>
        </div>
      </div>
      <div v-if="content && content.isVip" class="vipComponent">
        <p>{{ content.title }}</p>
        <p>温罄提示</p>
        <p>由于格式问题,该书籍暂不提供网页阅读,扫描如下</p>
        <p>二维码打开APP进行阅读~</p>
        <p>如已安装,直接启动应用即可</p>
        <img
          src="//statics.zhuishushenqi.com/msite/qrcode2app.png"
          class="qrcode"
        />
      </div>
      <!-- 控件 -->
      <div class="ctrl" v-if="isShowCtrl">
        <!-- 头部控件 -->
        <div class="ctrl-header">
          <van-nav-bar
            :title="bookDetailData.title"
            left-text="返回"
            left-arrow
            :fixed="true"
            @click-left="$router.go(-1)"
          >
            <template #right>
              <van-icon name="wap-nav" size="20px" color="#fff" />
              <van-sidebar>
                <a @click.prevent="$router.push('/')"
                  ><i></i><span>首页</span></a
                >
                <a @click.prevent="$router.push('/bookrack')"
                  ><i></i><span>书架</span></a
                >
                <a
                  @click.prevent="
                    $router.push({
                      path: '/detail',
                      query: {
                        id: $route.params.id,
                      },
                    })
                  "
                  ><i></i><span>详情</span></a
                >
              </van-sidebar>
            </template>
          </van-nav-bar>
        </div>
        <!-- 底部控件 -->
        <div class="ctrl-foother">
          <div class="myprogress">
            <h3
              @click.stop="
                if (fontSizeProgress == 0) {
                  $toast('字体已经最小');
                } else {
                  fontSize -= 2;
                }
                fontSizeProgress <= 0
                  ? (fontSizeProgress = 0)
                  : (fontSizeProgress -= 20);
              "
            >
              Aa-
            </h3>
            <van-progress
              stroke-width="2px"
              color="#b93221"
              track-color="#ccc"
              :show-pivot="false"
              :percentage="fontSizeProgress"
            />
            <h3
              @click.stop="
                if (fontSizeProgress == 100) {
                  $toast('字体已经最大');
                } else {
                  fontSize += 2;
                }
                fontSizeProgress >= 100
                  ? (fontSizeProgress = 100)
                  : (fontSizeProgress += 20);
              "
            >
              Aa+
            </h3>
          </div>
          <div class="btn-change">
            <van-button
              type="primary"
              color="transparent"
              :class="{ active: a }"
              @click.stop="
                a = true;
                b = false;
                c = false;
                bgc = { bg: '#EEE6DD', color: '#5C5D58' };
              "
              >默认</van-button
            >
            <van-button
              type="primary"
              color="transparent"
              :class="{ active: b }"
              @click.stop="
                b = true;
                a = false;
                c = false;
                bgc = { bg: '#0C0C0C', color: '#666666' };
              "
              >夜间</van-button
            >
            <van-button
              type="primary"
              color="transparent"
              :class="{ active: c }"
              @click.stop="
                c = true;
                b = false;
                a = false;
                bgc = { bg: '#B8CD8D', color: '#2F4E29' };
              "
              >护眼</van-button
            >
          </div>
          <div class="page">
            <span
              @click.prevent="
                if (order == 1) {
                  $toast('已经是第一章了~');

                  return;
                }
                isShowBigLoading = true;
                order = order - 2;
                axiosGet(true);
              "
              :style="{
                color: order == 1 ? '#888' : '#fff',
              }"
              >上一章</span
            >
            <!-- style="width: 30%; text-align: center; line-height: 50px" -->
            <span @click="isShowSidebar = true">目录</span>
            <span
              @click.prevent="
                isShowBigLoading = true;
                axiosGet(true);
              "
              :style="{
                color: order == this.chapter.chapters.length ? '#888' : '#fff',
              }"
              >下一章</span
            >
          </div>
        </div>
      </div>
      <div class="catalog">
        <!-- :overlay="" -->
        <van-popup
          v-model="isShowSidebar"
          position="left"
          :overlay="true"
          @click.stop="isShowSidebar = true"
          @click-overlay="isShowSidebar = false"
          :style="{
            width: '80%',
            height: '100%',
          }"
        >
          <div
            class="catalog-content"
            :style="{ marginTop: scrollY + 'px' }"
            @touchstart="touchStartH"
            @touchmove="touchMoveH"
            @touchend="touchEndH"
          >
            <p style="color: #333">{{ bookDetailData.title }}</p>
            <p>目录</p>
            <ol>
              <li
                v-for="(item, index) in chapter.chapters"
                :key="item.id"
                :class="{ before: item.isVip }"
                @click.stop="
                  isShowSidebar = false;
                  axiosGet(true, index);
                "
                :id="order - 1 == index ? 'anchor' : ''"
              >
                <!-- :style="{}" -->
                <span :class="{ activeC: order - 1 == index }"
                  >{{ index + 1 }}-{{ item.title }}</span
                >
              </li>
            </ol>
          </div>
        </van-popup>
      </div>
    </div>
    <div
      v-if="bookDetailData.contentType == 'picture'"
      @click="CtrlHandler"
      class="read mypicture"
    >
      <Cartoon
        :pictureData="pictureData"
        :pictureDataArray="pictureDataArray"
        v-if="!pictureData.isVip"
      />
      <div v-else class="vipComponent">
        <p>{{ pictureData.title }}</p>
        <p>温罄提示</p>
        <p>由于格式问题,该书籍暂不提供网页阅读,扫描如下</p>
        <p>二维码打开APP进行阅读~</p>
        <p>如已安装,直接启动应用即可</p>
        <img
          src="//statics.zhuishushenqi.com/msite/qrcode2app.png"
          class="qrcode"
        />
      </div>

      <!-- 控件 -->
      <div class="ctrl" v-if="isShowCtrl">
        <!-- 头部控件 -->
        <div class="ctrl-header">
          <van-nav-bar
            :title="bookDetailData.title"
            left-text="返回"
            left-arrow
            :fixed="true"
            @click-left="$router.go(-1)"
          >
            <template #right>
              <van-icon name="wap-nav" size="20px" color="#fff" />
              <van-sidebar>
                <a @click.prevent="$router.push('/')"
                  ><i></i><span>首页</span></a
                >
                <a @click.prevent="$router.push('/bookrack')"
                  ><i></i><span>书架</span></a
                >
                <a
                  @click.prevent="
                    $router.push({
                      path: '/detail',
                      query: {
                        id: $route.params.id,
                      },
                    })
                  "
                  ><i></i><span>详情</span></a
                >
              </van-sidebar>
            </template>
          </van-nav-bar>
        </div>
        <!-- 底部控件 -->
        <div class="ctrl-foother ctrl-foother-picture">
          <div class="page">
            <span
              @click.prevent="
                if (order == 1) {
                  $toast('已经是第一章了~');
                  return;
                }
                isShowBigLoading = true;
                order = order - 2;
                axiosGet(true);
              "
              :style="{ color: order == 1 ? '#888' : '#fff' }"
              >上一章</span
            >
            <!-- style="width: 30%; text-align: center; line-height: 50px" -->
            <span @click="isShowSidebar = true">目录</span>
            <span
              @click.prevent="
                isShowBigLoading = true;
                axiosGet(true);
              "
              :style="{
                color: order == this.chapter.chapters.length ? '#888' : '#fff',
              }"
              >下一章</span
            >
          </div>
        </div>
      </div>
      <div class="catalog">
        <!-- :overlay="true" -->
        <van-popup
          v-model="isShowSidebar"
          position="left"
          :overlay="true"
          @click.stop="isShowSidebar = true"
          @click-overlay="isShowSidebar = false"
          :style="{ width: '80%', height: '100%' }"
        >
          <div
            class="catalog-content"
            :style="{ marginTop: scrollY + 'px' }"
            @touchstart="touchStartH"
            @touchmove="touchMoveH"
            @touchend="touchEndH"
          >
            <p style="color: #333">{{ bookDetailData.title }}</p>
            <p>目录</p>
            <ol>
              <li
                v-for="(item, index) in chapter.chapters"
                :key="item.id"
                :class="{ before: item.isVip }"
                @click.stop="
                  isShowSidebar = false;
                  axiosGet(true, index);
                "
                :id="order - 1 == index ? 'anchor' : ''"
              >
                <span :class="{ activeC: order - 1 == index }"
                  >{{ index + 1 }}-{{ item.title }}</span
                >
              </li>
            </ol>
          </div>
        </van-popup>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";
import Cartoon from "@/views/Cartoon.vue";
export default {
  components: {
    Cartoon,
  },
  data() {
    return {
      content: null,
      textArr: [],
      bool: false,
      order: 1,
      flag: true,
      isShowCtrl: false,
      isShowSidebar: false,
      fontSizeProgress: 80,
      a: true,
      b: false,
      c: false,
      isShowBigLoading: false,
      fontSize: 22,
      bgc: { bg: "#EEE6DD", color: "#5c5d58" },
      isVip: false,
      isHeight: false,
      scrollY: 0,
      initialScrollY: 0,
      //picture
      pictureData: {},
      pictureDataArray: [],
    };
  },
  watch: {
    fontSizeProgress(n, o) {
      if (!(n == 0 || n == 100)) {
        if (n > o) {
          this.$toast("字体变大");
        } else {
          this.$toast("字体变小");
        }
      }
    },
    order(n) {
      let _books = JSON.parse(window.localStorage.getItem("books")) || [];
      if (!_books.length) return;
      _books = _books.map((item) => {
        if (item._id == this.$route.params.id) {
          return { ...item, currentOrder: n };
        }
        return item;
      });
      window.localStorage.setItem("books", JSON.stringify(_books));

      let _readBooks =
        JSON.parse(window.localStorage.getItem("readBooks")) || [];
      if (!_readBooks.length) return;
      _readBooks = _readBooks.map((item) => {
        if (item._id == this.$route.params.id) {
          return { ...item, currentOrder: n };
        }
        return item;
      });
      window.localStorage.setItem("readBooks", JSON.stringify(_readBooks));
    },
  },
  computed: {
    ...mapState(["chapter", "bookDetailData"]),
    // scrollY() {
    //   return this.order * -40;
    // },
  },
  created() {
    this.bool = true;
    this.isShowBigLoading = true;
    // 请求成功将节流阀重置
    this.flag = false;
    let _bookDetailData = {};
    let _this = this;
    function pro(url) {
      return new Promise((resolve, reject) => {
        _this.axios
          .get(url)
          .then((res) => {
            resolve(res);
          })
          .catch((err) => {
            console.error(err);
            reject(err);
          });
      });
    }
    //获取书籍详情
    pro("http://novel.kele8.cn/book-info/" + this.$route.params.id).then(
      (result) => {
        _bookDetailData = result.data;
      }
    );

    //获取书籍源
    pro(
      "http://novel.kele8.cn/book-sources?view=summary&book=" +
        this.$route.params.id
    )
      .then((resolve) => {
        //获取书籍章节列表
        return pro(
          "http://novel.kele8.cn/book-chapters/" + resolve.data[0]._id
        );
      })
      .then((res) => {
        this.$router.push("/book/" + this.$route.params.id);
        this.$store.commit("updateChapter", res.data);
        this.$store.commit("updateBookDetailData", _bookDetailData);

        let readBooks =
          JSON.parse(window.localStorage.getItem("readBooks")) || [];
        if (this.bookDetailData == null) return;
        //小说相关逻辑
        if (
          this.bookDetailData.contentType == "txt" ||
          this.bookDetailData.contentType == "epub"
        ) {
          //获取本地缓存相关逻辑
          if (window.localStorage.getItem(this.$route.params.id)) {
            this.content = JSON.parse(
              window.localStorage.getItem(this.$route.params.id)
            );
            this.order = this.content.order;
            this.scrollY = this.order < 10 ? 0 : (this.order + 1) * -41 + 246;

            let isAddReadBook = readBooks.some((item) => {
              return item._id == this.bookDetailData._id;
            });
            if (!isAddReadBook) {
              readBooks.unshift({
                ...this.bookDetailData,
                currentOrder: this.order,
              });
            } else {
              readBooks = readBooks.filter((item) => {
                return item._id !== this.bookDetailData._id;
              });
              readBooks.unshift({
                ...this.bookDetailData,
                currentOrder: this.order,
              });
            }
            window.localStorage.setItem("readBooks", JSON.stringify(readBooks));
            this.textArr.push({
              arr: this.setStr(),
              title: this.content.title,
            });
            this.bool = false;
            this.isShowBigLoading = false;
            // 请求成功将节流阀重置
            this.flag = true;
          } else {
            //没有本地缓存就去ajax请求
            //本地缓存没有就去请求
            pro(
              "http://novel.kele8.cn/chapters/" +
                encodeURIComponent(this.chapter.chapters[0].link)
            ).then((res) => {
              this.content = res.data.chapter;
              window.localStorage.setItem(
                this.$route.params.id,
                JSON.stringify(res.data.chapter)
              );
              //
              readBooks.unshift({
                ...this.bookDetailData,
                currentOrder: this.order,
              });
              window.localStorage.setItem(
                "readBooks",
                JSON.stringify(readBooks)
              );
              this.textArr.push({
                arr: this.setStr(),
                title: this.content.title,
              });
              this.bool = false;
              this.isShowBigLoading = false;
              // 请求成功将节流阀重置
              this.flag = true;
            });
          }
          //漫画相关逻辑
        } else if (this.bookDetailData.contentType == "picture") {
          if (window.localStorage.getItem(this.$route.params.id)) {
            this.pictureData = JSON.parse(
              window.localStorage.getItem(this.$route.params.id)
            );
            this.order = this.chapter.chapters.filter((item) => {
              return this.pictureData.id == item.id;
            })[0].order;

            let isAddReadBook = readBooks.some((item) => {
              return item._id == this.bookDetailData._id;
            });
            if (!isAddReadBook) {
              readBooks.unshift({
                ...this.bookDetailData,
                currentOrder: this.order,
              });
            } else {
              readBooks = readBooks.filter((item) => {
                return item._id != this.bookDetailData._id;
              });
              readBooks.unshift({
                ...this.bookDetailData,
                currentOrder: this.order,
              });
            }
            window.localStorage.setItem("readBooks", JSON.stringify(readBooks));

            this.pictureDataArray.push(this.pictureData);
            this.bool = false;
            this.isShowBigLoading = false;
            // 请求成功将节流阀重置
            this.flag = true;
          } else {
            //没有本地缓存就去ajax请求
            pro(
              "http://novel.kele8.cn/picture/" +
                encodeURIComponent(this.chapter.chapters[0].link)
            ).then((res) => {
              this.pictureData = res.data.chapter;
              this.order = this.chapter.chapters.filter((item) => {
                return this.pictureData.id == item.id;
              })[0].order;
              this.pictureDataArray.push(this.pictureData);
              readBooks.unshift({
                ...this.bookDetailData,
                currentOrder: this.order,
              });
              window.localStorage.setItem(
                "readBooks",
                JSON.stringify(readBooks)
              );
              window.localStorage.setItem(
                this.$route.params.id,
                JSON.stringify(res.data.chapter)
              );
              this.bool = false;
              this.isShowBigLoading = false;
              // 请求成功将节流阀重置
              this.flag = true;
            });
          }
        }
      });
    // Promise.all([pro]).then((res) => {
    //   console.log(res);
    // });
  },

  mounted() {
    window.addEventListener(
      "scroll",
      () => {
        //如果flag为false就表示在请求

        if (!this.flag || this.bookDetailData == null) {
          return;
        }
        //如果flag为false就表示在请求
        if (
          window.scrollY + window.outerHeight + 10 >=
          document.documentElement.offsetHeight
        ) {
          this.flag = false;
          setTimeout(() => {
            if (
              this.bookDetailData.contentType == "txt" ||
              this.bookDetailData.contentType == "epub"
            ) {
              //如果触底就请求下一章
              // loading开启
              this.bool = true;
              this.axiosGet();
            } else if (this.bookDetailData.contentType == "picture") {
              this.flag = false;
              this.axiosGet();
            }
          }, 300);
        }
      },
      false
    );
  },

  methods: {
    setStr() {
      let reg = /\n/g;
      if (!reg.test(this.content.cpContent)) {
        // console.log(22222);
        if (!this.order) {
          this.order = 1;
        }
        // this.order = 1;
        this.isHeight = true;
        return [this.content.body];
      }
      this.isHeight = false;
      let str = this.content.cpContent.replace(reg, (y) => {
        y = "|";
        return y;
      });
      return str.split("|");
    },
    axiosGet(bool, i) {
      let ind = 0;
      if (i >= 0) {
        ind = i;
      } else {
        ind = this.order;
      }
      // console.log(i);
      // console.log("axiosGet");
      // console.log(this.chapter.chapters.length);
      console.log(!this.chapter.chapters[ind]);
      if (!this.chapter.chapters[ind]) {
        this.$toast("已经到了最后一章~");
        return;
      }

      this.bool = true;
      // }
      if (this.bookDetailData == null) return;
      // 获取小说相关逻辑
      if (
        this.bookDetailData.contentType == "txt" ||
        this.bookDetailData.contentType == "epub"
      ) {
        // if (this.chapter.chapters[this.order].isVip) {
        //   this.bool = false;
        // }
        //获取章节详情
        this.axios
          .get(
            "http://novel.kele8.cn/chapters/" +
              encodeURIComponent(this.chapter.chapters[ind].link)
          )
          .then((res) => {
            if (bool) {
              this.textArr = [];
            }
            // 设置本地缓存
            window.localStorage.setItem(
              this.$route.params.id,
              JSON.stringify(res.data.chapter)
            );
            // 获取数据
            this.content = res.data.chapter;
            this.order = this.content.order;
            this.isVip = this.content.isVip;
            this.textArr.push({
              arr: this.setStr(),
              title: this.content.title,
            });
            this.bool = false;
            this.isShowBigLoading = false;
            // 请求成功将节流阀重置
            this.flag = true;
          });
        // .finally(() => {
        //   // loading关闭
        //   this.bool = false;
        //   this.isShowBigLoading = false;
        //   // 请求成功将节流阀重置
        //   this.flag = true;
        // });
      } else if (this.bookDetailData.contentType == "picture") {
        // if (this.chapter.chapters[this.order].isVip) {
        //   this.bool = false;
        // }
        //获取漫画章节详情
        this.axios
          .get(
            "http://novel.kele8.cn/picture/" +
              encodeURIComponent(this.chapter.chapters[ind].link)
          )
          .then((res) => {
            if (bool) {
              this.pictureDataArray = [];
            }
            // 设置本地缓存
            window.localStorage.setItem(
              this.$route.params.id,
              JSON.stringify(res.data.chapter)
            );
            this.pictureDataArray.push(res.data.chapter);

            // 获取数据
            this.pictureData = res.data.chapter;
            this.order = this.chapter.chapters[ind].order;
          });
        // .finally(() => {
        //   // loading关闭
        //   this.bool = false;
        //   this.isShowBigLoading = false;
        //   // 请求成功将节流阀重置
        //   this.flag = true;
        // });
      }
    },
    CtrlHandler() {
      this.isShowCtrl = !this.isShowCtrl;
    },
    touchStartH(e) {
      this.initialScrollY = e.touches[0].pageY - e.path[2].offsetTop;
    },
    touchMoveH(e) {
      // console.log(this.$refs.oDiv);
      // this.$refs.oDiv.style.marginTop = 0;
      // console.log(e.touches[0].pageY - this.initialScrollY);
      this.scrollY = e.touches[0].pageY - this.initialScrollY;
      this.scrollY = this.scrollY > 0 ? 0 : this.scrollY;
      if (-this.scrollY + 246 > this.chapter.chapters.length * 41) {
        this.scrollY = -this.chapter.chapters.length * 41 + 246;
      }
      console.log(this.scrollY);
    },
    touchEndH(e) {
      console.log(e);
      // this.initialScrollY = e.touches[0].pageY;
    },
  },
};
</script>

<style lang="less" scpoed>
.read {
  padding: 0 20px;
  // width: 100%;
  // height: 500px;
  // margin: 0 0 50px 0;
  height: 100%;

  .ctrl-foother {
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100vw;
    height: 200px;
    padding: 0 20px;
    box-sizing: border-box;
    background-color: rgba(50, 51, 52, 0.9);
    .myprogress {
      display: flex;
      // justify-content: ;
      align-items: center;
      padding: 10px;
      h3 {
        display: inline-block;
        padding: 0 10px;
        color: #fff;
        font-weight: 400;
      }
      .van-progress {
        display: inline-block;
        flex: 1;
        // width: 80%;
      }
    }
